.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* min-height: 100vh; */
  background-color: #fafafa
}

.textbox {
  width: 100%;
  color: black;
  max-width: 40%;
  padding: 1rem;
  margin-bottom: 1rem;
  border: 1px solid black;
  border-radius: 6px;
}

.button {
  padding: 0.75rem 1.5rem;
  background-color: #0070f3;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
}

.button:disabled {
  background-color: #aaa;
  cursor: not-allowed;
}

.error {
  color: red;
  margin-top: 0.5rem;
  font-size: 0.9rem;
}

.audio {
  margin-top: 2rem;
}
